.todo-form-container {
	margin-bottom: 16px;
	padding: 16px;
	background: white;
	box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
	border-radius: 10px;
}

.todo-form-container input {
	color: #4d4d4d;
	font-size: 24px;
	font-weight: 300;
	line-height: 33px;
	font-family: 'Lato', sans-serif;
	width: 100%;
	height: 33px;
	outline: none;
	border: none;
}
